import React, { useState } from 'react';
import styled from "styled-components";

const Footer = () => {
  const [showPrivacyPolicy, setShowPrivacyPolicy] = useState(false);
  const [showTermsOfUse, setShowTermsOfUse] = useState(false);

  const handlePrivacyPolicy = () => {
    setShowPrivacyPolicy(true);
  };

  const handleTermsOfUse = () => {
    setShowTermsOfUse(true);
  };

  return (
    <FooterContainer>
      <PrivacyPolicy onClick={handlePrivacyPolicy}></PrivacyPolicy>
      <TermsOfUse onClick={handleTermsOfUse}></TermsOfUse>
      <CopyrightContainer>
        Copyright  2023 mass
      </CopyrightContainer>
      {showPrivacyPolicy && (
        <Modal>
          <ModalTitle></ModalTitle>
          <ModalContent>
          <h2></h2>
          <h3></h3>
                <p></p>
                <p></p>
            <h3>Cookie</h3>
                <p>Cookie</p>
                <p>Cookie</p>
                <p>Cookie</p>
            <h3></h3>
                <p>GoogleGoogle</p>
                <p>Cookie</p>
            <CloseButton onClick={() => setShowPrivacyPolicy(false)}></CloseButton>
          </ModalContent>

        </Modal>
      )}
      {showTermsOfUse && (
        <Modal>
          <ModalTitle></ModalTitle>
          <ModalContent>
            <h2></h2>
            <h3>1.</h3>
                <p></p>
            <h3>1.</h3>
                <p></p>
                <p></p>
                <p></p>
            <h3>3.</h3>
                <p>PDF</p>
                <p></p>
            <h3>4.</h3>
                <p></p> 
                <p></p>
            <h3>5.</h3>
                <p></p>
                <p></p>
            <h3>6.</h3>
                <p></p>
                <p></p>
            <CloseButton onClick={() => setShowTermsOfUse(false)}></CloseButton>
          </ModalContent>
          
        </Modal>
      )}
    </FooterContainer>
  );
};

export default Footer;

const FooterContainer = styled.div`
  text-align: center;
  background-color: rgb(60, 60, 60);
  margin: 0% 0% -100% 0%;
  padding: 0% 0% 100% 0%;
`;

const PrivacyPolicy = styled.button`
  color: rgb(210, 210, 210);
  text-decoration: none;
  font-size: 0.8rem;
  border: none;
  background-color: transparent;
  cursor: pointer;
`;

const TermsOfUse = styled.button`
  color: rgb(210, 210, 210);;
  text-decoration: none;
  font-size: 0.8rem;
  border: none;
  background-color: transparent;
  cursor: pointer;
`;

const Modal = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
`;

const ModalTitle = styled.h2`
  font-size: 1.5rem;
  margin-bottom: 1rem;
`;

const ModalContent = styled.div`
background-color: rgb(40, 40, 40);
text-align: left;
  font-size: 1rem;
  line-height: 1;
  margin: 1rem;
  padding: 2%;
  border-radius: 20px;
`;

const CloseButton = styled.button`
  font-size: 1rem;
  padding: 0.5rem 1rem
`
const CopyrightContainer = styled.div`
    
`